<template>
  <div class="dialog" v-if="showing">
    <div class="dialog_content">
      <div class="dialog_name">
        <img :src="closeImage" class="close" @click="onClose">
        <div class="text">{{ props.name }}</div>
      </div>

      <div class="slot">
        <slot></slot>
      </div>

    </div>
  </div>
</template>

<script setup>
import closeImage from "../assets/images/icon/close.svg"
import { computed, defineProps, defineEmits } from 'vue'
// import cImage from "../assets/images/1778b7d044ba70b8b3cf35f6a0e8b63.jpg"
const props = defineProps({
  showing: String,
  name: String
})

const emit = defineEmits(['update:showing'])

const showing = computed({
  get() {
    return props.showing
  },
  set(value) {
    emit('update:showing', value)
  }
})

function onClose() {
  showing.value = false
}

</script>

